@import url(https://fonts.googleapis.com/css2?family=Black+Ops+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Carter+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Contrail+One&family=Courgette&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Secular+One&display=swap);
* {
  box-sizing: border-box;
  padding:0;
  margin:0;
}

/* CUSTOM SCROLL BAR    */
::-webkit-scrollbar {
    scroll-behavior: smooth;
    width: 10px;
    height: 8px;
}


/* Track */
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: rgba(194, 153, 153, 0.8); 
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}
::-webkit-scrollbar-thumb:window-inactive {
	background: rgba(255,0,0,0.4); 
}
.nav__bar{
    background:rgba(0,0,0,0.3) ;
    display: flex;
    align-items: center;
    padding: 5px 10px 0 10px;
    position: fixed;
    top: 0;
    height: 50px;
    width: 100%;
    z-index: 1000;
    /* opacity: 1; */

}
.right__items{
    display:flex;
}    
.right__items li{
    list-style: none;
    margin: 0 15px;
}
.text__link{
    color: white;
    transition: all 0.2s ;
    font-size: 16px;
}
.text__link:hover{
    background-color: whitesmoke;
    color: black;
    border-radius: 3px;
    transform: scale(1.05);
    display: inline-block;
}
.logo{
    flex: 1 1;
}


.card{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    margin: 0 10px;
    /* background-color: aqua; */
    height: 390px;
    

}
.poster{
    object-fit: contain;
    width: 200px;
    height: 300px;
    border-radius:5px;
    transition: all 0.5s;
}
.poster__title{
    margin-top: 10px;
    font-family: 'Bungee Inline', cursive;
    color: wheat;
}
.poster:hover{
    transform : scale(1.08);
}

.cards__container{
    display:flex;
    flex-flow: row nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
    padding: 15px 0;   
}
.row{
    margin-top:50px;
    /* margin-bottom:70px; */
}

.heading{
    font-family: 'Black Ops One', cursive;
    font-size: 40px;
    color: white;
}
.car{
    border-radius: 15px;
    padding-top: 5px;
    background-color:black;
    margin-right: 20px;
    margin-bottom: 50px;
    background-image:
    radial-gradient(white, rgba(255,255,255,.2) 1px, transparent 5px),
    radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 5px),
    radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 5px),
    radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 5px);
    background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
    background-position: 0 0, 40px 60px, 130px 270px, 70px 100px; 
} 

.indiv{
    height: 80vh;
    border-radius: 15px;
}
.image{
    object-fit: contain;
    height: 100%;
    border-radius: 5px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
}
.homepage{
    margin-top: 0px;
    padding-left: 20px;
    padding-top: 120px;
    background-color: #100E17;
}

.community-container{
    padding-top:100px;
}
.tv__show{
    padding-top: 0;
}
.cardDetail__image{
    min-height: 100%;
    min-width: 100%;
    border-radius: 10px;
    object-fit: contain;
    box-shadow: 0 4px 8px rgba(0,0,0,0.4);
}
.CardDetail__detail{
    max-width: 750px;
    margin-left: 20px;
}
.CardDetail__container{
    background-attachment: fixed;
    display: flex;
    min-height:100vh ;
    flex-flow: row nowrap;
    justify-content: space-around;
    padding-top: 60px;
    align-items: center;
    max-width: 100%;
    
}
.overview__header, .CardDetail__title, .date, .popularity, .overview{
    color: white;
}
.CardDetail__title{
    font-family: 'Carter One', cursive;
    font-size: 45px;
    font-weight: 500;
}

.overview__detail{
    font-family: 'Contrail One', cursive;
    font-family: 'Courgette', cursive;
    font-size: 21px;
    font-weight: 100;
}
.overview__header{
    font-family: 'Secular One', sans-serif;
    font-weight: 400;
    font-size: 35px;
}

@media screen and (max-width:793px){
    .CardDetail__container{
        flex-flow: column nowrap;
        align-items: center;
        
    }
    .CardDetail__detail{
        margin-top: 20px;
        max-width: 400px;
        margin-right: 5px;
        text-align: left;
        margin-left:60px;
    }
    
    .overview__detail{
        font-size: 19px;
    }
    .CardDetail__title{
        font-size: 35px;
    }
    .overview__header{
        font-size: 30px;
    }
}
@media screen and (max-width:460px){
    .CardDetail__detail{
          margin-left: 40px;
    }
}
@media screen and (max-width:411px){
    .CardDetail__detail{
        max-width: 400px;
        margin-right: 10px;
        margin-left: 20px;
        text-align: left;
    }
    .cardDetail__image{
        height: 50%;
        width: 50%;
        /* object-fit: none; */
    }
}

